import { Component, h, Prop } from '@stencil/core';
import { Touchscreen } from 'puppeteer';
import { Items } from './breadcrumbs.types';
/**
 *
 * @exampleComponent limel-example-breadcrumbs-basic
 */

@Component({
    tag: 'limel-breadcrumbs',
    shadow: true,
    styleUrl: 'breadcrumbs.scss',
})
export class Breadcrumbs {
    @Prop()
    public items: Items[];

    @Prop()
    public isButton: boolean = false;

    public render() {
        console.log(this.items);

        return (
            <nav aria-label="Breadcrumb">
                <ol>
                    <li>{this.renderStep(this.items)}</li>

                    <li>
                        <span aria-current="page">
                            {this.items[this.items.length - 1]}
                        </span>
                    </li>
                </ol>
            </nav>
        );
    }

    private renderStep = (items) => {
        if (this.isButton) {
            return (
                <div class="breadcrumbs">{items.map(this.renderButtons)}</div>
            );
        }

        return <div class="breadcrumbs">{items.map(this.renderLinks)}</div>;
    };

    private renderLinks = (item: Items) => {
        return (
            <a href={item.url}>
                <span>{item.name}</span>
            </a>
        );
    };

    private renderButtons = (item: Items) => {
        return (
            <limel-icon-button
                icon={item.icon}
                label={item.name}
                // onClick={item.url}
            />
        );
    };

    private renderIcon = (item: Items) => {
        if (!item.icon) {
            return;
        }

        return <limel-icon name={item.icon} />;
    };
}
